.documents-list {
  margin-top: 64px;
  margin-bottom: 84px;

  &__title {
    position: relative;
    max-width: 278px;
    margin-bottom: 40px;
    font-size: 24px;
    font-weight: 700;
    line-height: 32px;
    color: $black;

    &:before {
      content: '';
      position: absolute;
      top: 22px;
      left: -112px;
      width: 66px;
      height: 1px;
      background-color: $red;
    }
  }

  &__list {
    margin-top: 40px;
  }

  &__link {
    display: flex;

    &:not(:last-of-type) {
      margin-bottom: 24px;
    }

    &:hover {
      & .documents-list__icon-wrapper {
        background-color: $red;

        & svg {
          fill: $white;
        }
      }

      & .documents-list__link-title {
        color: $red;
      }
    }

    &-title {
      margin-bottom: 4px;
      font-size: 15px;
      line-height: 24px;
      color: $black;
      @include commonTransition;
    }
  }

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    margin-right: 24px;
    border-radius: 50%;
    background-color: rgba(235, 28, 36, .1);
    @include commonTransition;

    & svg {
      margin: auto;
      fill: $red;
      @include commonTransition;
    }
  }
}
