.hall-item {
  & .col {
    &_short {
      box-sizing: border-box;
      padding-right: 40px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &:not(:last-of-type) {
      margin-bottom: 32px;
    }

    &:last-of-type {
      position: relative;
      padding-top: 16px;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 1px;
        background-color: rgba(12, 154, 221, .4);
      }
    }

    & svg {
      margin-bottom: 16px;
    }
  }

  &__text {
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__value {
    font-size: 15px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
  }

  &__img {
    height: 300px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }

  &__content {
    box-sizing: border-box;
    max-width: calc(100% - 24px);
    margin-left: auto;
    padding: 24px 32px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__title {
    display: inline-block;
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-decoration: none;
    color: $black;
    @include commonTransition;

    &:hover {
      color: $red;
    }
  }
}
