.common-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 32px;

  &__item {

  }

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-bottom: 16px;
    border-radius: 50%;
    background-color: rgba(12, 154, 221, .1);

    & svg {
      margin: auto;
      fill: $blue;
    }
  }

  &__text {
    margin-bottom: 4px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__link,
  &__value {
    font-size: 15px;
    font-weight: 700;
    line-height: 20px;
    color: $black;
  }

  &__link {
    text-decoration: none;
    @include commonTransition;

    &:not(:last-of-type) {
      margin-bottom: 4px;
    }

    &:hover {
      color: $red;
    }
  }

  &__value {
    margin: 0;
  }

  &__unique-link {
    font-size: 15px;
    font-weight: 700;
    line-height: 20px;
    text-decoration: underline;
    color: $red;

    &:hover {
      text-decoration: none;
    }
  }

  &__social {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;

    &-link {
      font-size: 15px;
      font-weight: 700;
      line-height: 24px;
      color: $black;
      @include commonTransition;

      &:hover {
        color: $red;
      }
    }
  }
}
