.subsections-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 24px;
  padding-top: 120px;

  &__title {
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 24px;
    padding-left: 24px;
    border-left: 1px solid rgba(12, 154, 221, .4);
  }

  &__link {
    position: relative;
    display: inline-block;
    font-size: 13px;
    line-height: 18px;
    color: $black;
    @include commonTransition;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: -25px;
      width: 1px;
      height: 18px;
      opacity: 0;
      background-color: $blue;
      @include commonTransition;
    }

    &:hover {
      color: $blue;

      &:before {
        opacity: 1;
      }
    }

    &:not(:last-of-type) {
      margin-bottom: 16px;
    }
  }
}
