.service-item {
  & .col {
    &_short {
      padding-right: 40px;
    }
  }

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 186px;
    border: 1px solid rgba(12, 154, 221, .4);
    background-color: $white;
  }

  &__icon {
    margin: auto;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 30px;
    text-decoration: none;
    color: $black;
    @include commonTransition;

    &:hover {
      color: $red;
    }
  }

  &__description {
    margin-top: 8px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;
    margin-top: 16px;
    margin-bottom: 16px;
    padding-left: 0;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(12, 154, 221, .2);
    list-style: none;

    &-link {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      text-decoration: none;

      & svg {
        fill: $blue;
      }

      &-text {
        margin-left: 8px;
        color: $blue;
      }
    }
  }
}
