.common-menu {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 96px;

  &__item {
    width: 33%;
    margin-bottom: 16px;
    cursor: pointer;

    &_active {
      & .dropdown-menu {
        display: block;
      }
    }

    &:nth-of-type(3n) {
      margin-bottom: 0;
    }

    &:hover {
      & .common-menu__dd {
        display: block;
      }
    }

    &-text {
      display: inline-block;
      font-size: 13px;
      font-weight: 400;
      color: $black;
      @include commonTransition;

      &_current {
        color: $red;
      }

      &:hover {
        color: $red;
      }
    }
  }
}
