.gallery-item {
  margin-bottom: 64px;

  &__content {
    max-width: 384px;
  }

  &__title {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
    line-height: 36px;
    color: $black;
  }

  &__description {
    margin-bottom: 24px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
  }

  &__img {
    height: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }
}
