.event-type-card {
  position: relative;
  display: inline-block;
  max-width: 333px;

  &:hover {
    & .event-type-card__mask {
      opacity: 1;
    }

    & .event-type-card__img {
      transform: scale(1.05);
    }

    & .hover-block__item_visible {
      transform: translate3d(0, -100%, 0);
    }

    & .hover-block__item_hover {
      transform: translate3d(0, 0, 0);
    }
  }

  &__img-wrapper {
    position: relative;
    overflow: hidden;
    background-color: rgba(12, 154, 221, .1);
  }

  &__mask {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .25);
    @include commonTransition;
  }

  &__img {
    height: 278px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include commonTransition;
  }

  &__btn {
    position: relative;
    overflow: hidden;
    z-index: 2;
    box-sizing: border-box;
    min-height: 58px;
    margin-left: 16px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__title {
    margin: 0;
    padding: 16px 24px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
  }
}
