.person-contacts-info {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin-bottom: 64px;
  padding: 32px;
  border: 1px solid rgba(12, 154, 221, .4);

  &__main-row,
  &__bottom-row {
    display: flex;
  }

  &__bottom-row {
    margin-top: 16px;
    padding-top: 16px;
    border-top: 1px solid rgba(12, 154, 221, .4);
  }

  &__col {
    box-sizing: border-box;
    width: 33%;
    padding-right: 40px;
  }

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-bottom: 16px;
    border-radius: 50%;
    background-color: rgba(12, 154, 221, .1);

    & svg {
      margin: auto;
      fill: $blue;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__name {
    margin-bottom: 4px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
  }

  &__text {
    font-size: 13px;
    line-height: 18px;
    color: $gray;
  }

  &__link {
    display: inline-block;
    margin-bottom: 4px;
    font-size: 15px;
    font-weight: 700;
    line-height: 24px;
    text-decoration: none;
    color: $black;
    @include commonTransition;

    &:hover {
      color: $red;
    }
  }

  &__item {
    display: flex;
  }

  &__unique-link {
    display: inline-block;
    margin-left: 6px;
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    text-decoration: none;
    color: $blue;
    @include commonTransition;

    &:hover {
      color: $red;
    }
  }
}
