.footer {
  min-height: 255px;
  margin-bottom: 44px;

  &__column {
    box-sizing: border-box;
    padding-left: 24px;
    padding-right: 24px;

    &_short {
      background-repeat: no-repeat;
      background-size: cover;
    }

    &_long {
      padding-top: 24px;
      padding-bottom: 24px;

      & .row {
        height: 100%;
      }

      & .col {
        &_short,
        &_long {
          display: flex;
          flex-direction: column;
        }

        &_short {
          align-items: flex-start;
        }

        &_long {
          padding-left: 40px;
        }
      }
    }
  }

  & .logo {
    &:hover {
      & .logo__mask {
        transform: translate3d(0, 0%, 0);
      }
    }
  }

  & p {
    font-size: 13px;
    line-height: 20px;
  }

  &__copyright {
    max-width: 120px;
    margin-top: 54px;
    color: $black;
  }

  &__contacts-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 16px;

    & .link {
      &:not(:last-of-type) {
        margin-bottom: 16px;
      }

      &:hover {
        color: $blue;
      }
    }
  }

  &__text {
    margin-top: 0;
    color: $gray;
  }

  & .social-block {
    margin-top: auto;

    &__link {
      color: $white;

      &:hover {
        color: $blue;
      }
    }
  }

  & .common-menu {
    &__link {
      color: $white;

      &:hover {
        color: $blue;
      }
    }
  }

  &__bottom-row {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 40px;
    border-top: 1px solid $white;

    & .link {
      max-width: 172px;

      &:hover {
        color: $blue;
      }
    }
  }

  &__creator-link {
    max-width: 148px !important;
  }
}
