.header {
  position: relative;
  z-index: 100;

  &_fixed {
    & .header__light {
      transform: translateY(0);

      & .dropdown-menu {
        top: 62px;
      }
    }
  }

  &__complicated {
    & .logo {
      &:hover {
        & .logo__mask {
          transform: translate3d(0, 0%, 0);
        }
      }
    }
  }

  & .social-block {
    margin-top: 16px;
  }

  &__col {
    &_long {
      position: relative;
      padding-top: 24px;
      padding-bottom: 24px;
    }
  }

  &__light {
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    min-height: 62px;
    border-bottom: 1px solid transparent;
    background-color: $white;
    transform: translateY(-63px);
    will-change: transform;
    @include commonTransition;

    & .logo {
      display: inline-block;
      vertical-align: top;
      width: 140px;
      height: 23px;
      background-color: transparent;
    }

    & .row {
      align-items: center;
    }

    & .col {
      &_long {
        position: relative;
      }
    }

    & .common-menu {
      display: none;

      &__item {
        flex-grow: 1;
        margin-right: 0 !important;
        padding-top: 22px;
        padding-bottom: 22px;
      }
    }

    & .dropdown-menu {
      & .row {
        align-items: flex-start;
      }
    }
  }
}

.logo {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 115px;
  height: 136px;
  background-color: $red;

  &__mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 115px;
    height: 136px;
    opacity: .15;
    background-color: $white;
    transform: translate3d(0, 100%, 0);
    @include commonTransition;
  }

  & svg {
    position: relative;
    z-index: 2;
    margin: auto;
  }
}

.m-common-menu {
  &_horizontal {
    & .common-menu {
      flex-direction: row;
      justify-content: space-between;
      max-height: none;

      &__item {
        width: auto;
        margin-bottom: 0;

        &:not(:last-of-type) {
          margin-right: 16px;
        }

        &:hover {
          & .dropdown-menu {
            display: block;
          }
        }
      }
    }
  }
}
