.main-slider-slide {
  &__main-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    height: 100%;
    padding-right: 40px;

    & .arrow-btn {
      margin-top: 30px;
    }
  }

  &__text {
    margin-top: 0;
    margin-bottom: 12px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;

    &_red {
      color: $red;
    }
  }

  &__title {
    display: inline-block;
    margin-top: 2px;
    margin-bottom: 18px;
    font-size: 28px;
    font-weight: 700;
    line-height: 36px;
    color: $black;
    @include commonTransition;

    &:hover {
      color: $red;
    }
  }
}
