.dropdown-menu {
  position: absolute;
  top: 136px;
  left: 0;
  box-sizing: border-box;
  display: none;
  width: 100%;
  padding-top: 40px;
  padding-bottom: 40px;
  border-top: 1px solid rgba(12, 154, 221, .4);
  background-color: $white;
  cursor: default;

  & .col {
    box-sizing: border-box;

    &_short {
      padding-left: 40px;
    }
  }

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-bottom: 8px;
    border-radius: 50%;
    background-color: rgba(235, 28, 36, .1);

    & svg {
      margin: auto;
    }
  }

  &__icon {
    fill: $red;
  }

  &__title {
    display: inline-block;
    margin-top: 8px;
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 700;
    text-decoration: none;
    color: $black;

    &:hover {
      color: $red;
    }
  }

  &__nav {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: baseline;
    max-height: 154px;

    &-link {
      box-sizing: border-box;
      display: inline-block;
      width: 33%;
      padding-right: 60px;
      font-size: 13px;
      line-height: 18px;
      color: $black;
      @include commonTransition;

      &:hover {
        color: $red;
      }

      &:not(:last-of-type) {
        margin-bottom: 16px;
      }

      &:nth-of-type(4n) {
        margin-bottom: 0;
      }
    }
  }
}
