/**
 * Common styles
 */

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
  -ms-overflow-style: scrollbar;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: 100%;
}

body {
  font-family: 'ClearSans', 'Verdana', 'Times New Roman', sans-serif;
  font-weight: 400;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: transform 0.6s ease-out 0s, opacity 0.6s ease-out 0s;
}

.bg-color {
  &_purpleBlue {
    background-color: $purpleBlue;
  }
}

/**
 * Typography
 */

a {
  display: inline-block;
  font-size: 13px;
  font-weight: 400;
  text-decoration: none;
  @include commonTransition;
}

.section-title {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
  color: $black;
}

h1 {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 28px;
  font-weight: 700;
  line-height: 36px;
  color: $black;
}

h2 {
  margin-top: 64px;
  margin-bottom: 24px;
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
  color: $black;
}

p {
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 15px;
  line-height: 24px;
  color: $black;

  & a {
    font-size: 15px;
    text-decoration: underline;
    color: $red;

    &:hover {
      text-decoration: none;
    }
  }
}

/**
 * Form controls
 */

input,
textarea,
button {
  box-shadow: none;
}

input,
textarea,
select {
  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
  }
}

input {
  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  &::-ms-clear {
    display: none;
    width : 0;
    height: 0;
  }

  &::-ms-reveal {
    display: none;
    width : 0;
    height: 0;
  }
}

input[type='number'] {
  -moz-appearance: textfield;
}

input[type='date'] {
  &::-webkit-inner-spin-button,
  &::-webkit-calendar-picker-indicator {
    display: none;
    -webkit-appearance: none;
  }
}

/**
 * Grid
 */

.common-wrapper {
  position: relative;
  min-height: 598px;
  padding-bottom: 120px;
}

.common-container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 24px;
}

.row {
  display: flex !important;
  width: 100% !important;
}

.main-row {
  height: 100%;

  &__col {
    &_short {
      display: flex;
      flex-direction: column;
    }
  }
}

.common-section {
  margin-bottom: 120px;

  &__heading {
    margin-bottom: 40px;
  }
}

.col {
  &_short {
    width: 30%;
  }

  &_long {
    width: 70%;
  }
}

.hidden-xs {
  @include extra_small_max {
    display: none;
  }
}

.visible-xs {
  display: none;

  @include extra_small_max {
    display: block;
  }
}

/**
 * Main styles
 */

@keyframes customFadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.customFadeInUp {
  opacity: 0;
  transform: translateY(20px);
  animation-name: customFadeInUp;
  animation-fill-mode: forwards;
  transition: transform 0.6s ease-out 0s, opacity 0.6s ease-out 0s;
}

// Index page

.index-events-top {
  & .section-title {
    max-width: 278px;
  }

  &__grid {
    display: flex;
  }

  &__column {
    &_left {
      flex-grow: 1;
      margin-right: 40px;
    }

    &_right {
      max-width: 384px;
      width: 100%;

      & .event-top-card {
        &:not(:last-of-type) {
          margin-bottom: 40px;
        }
      }
    }
  }
}

.index-events-type {
  margin-bottom: 180px;

  & .section-title {
    max-width: 278px;
    margin-bottom: 0;
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 0 40px;

    & .event-type-card {
      &:first-of-type {
        & .event-type-card__btn {
          border-bottom: 2px solid $red;
        }
      }

      &:nth-of-type(2) {
        top: 80px;

        & .event-type-card__btn {
          border-bottom: 2px solid $blue;
        }
      }

      &:last-of-type {
        top: -40px;

        & .event-type-card__btn {
          border-bottom: 2px solid $purpleBlue;
        }
      }
    }
  }
}

.index-about {
  margin-bottom: 0;

  & .common-section__heading {
    display: flex;
  }

  &__column {
    width: 50%;

    &_left {
    }

    &_right {
      padding-top: 60px;
    }
  }

  &__content {
    max-width: 278px;
  }

  &__paragraph {
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }
}

.info-slider {
  & .slider {
    height: 400px;
  }
}

.info-values-slider {
  width: 50%;
  margin: auto;

  &__slide {
    display: flex !important;
    flex-direction: column;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: flex-end;
  }

  &__title {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    color: $red;
  }

  &__value,
  &__unit {
    font-weight: 700;
    color: $red;
  }

  &__value {
    font-size: 56px;
    line-height: 54px;
  }

  &__unit {
    margin-left: 12px;
    font-size: 22px;
  }

  &__text {
    font-size: 15px;
    line-height: 24px;
    color: $black;
  }
}

// Calendar

.calendar-wrapper {
  padding-bottom: 32px;
  border: 1px solid rgba(12, 154, 221, .4);

  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 24px;
    margin-right: 24px;
    border-bottom: 1px solid rgba(12, 154, 221, .4);
  }

  &__title {
    font-size: 13px;
    font-weight: 700;
    line-height: 24px;
    color: $gray;
  }

  &__main {
    display: flex;
    align-items: center;
    margin: 24px 24px 16px;
  }

  &__date {
    display: flex;

    &-month,
    &-year {
      font-size: 22px;
      font-weight: 700;
      line-height: 28px;
      color: $black;
    }

    &-month {

    }

    &-year {
      margin-left: 6px;
    }
  }
}

.event-tabs {
  display: flex;

  &__item {
    padding: 15px 0;
    border-bottom: 1px solid transparent;
    font-size: 13px;
    font-weight: 700;
    line-height: 20px;
    color: $black;
    cursor: pointer;

    &_all {
      color: $black;

      &.event-tabs__item_active {
        border-bottom-color: $black;
      }
    }

    &_type-1 {
      color: $red;

      &.event-tabs__item_active {
        border-bottom-color: $red;
      }
    }

    &_type-2 {
      color: $blue;

      &.event-tabs__item_active {
        border-bottom-color: $blue;
      }
    }

    &_type-3 {
      color: $purpleBlue;

      &.event-tabs__item_active {
        border-bottom-color: $purpleBlue;
      }
    }

    &:not(:last-of-type) {
      margin-right: 24px;
    }
  }
}

.event_type_1 {
  background-color: $red !important;
}

.event_type_2 {
  background-color: $blue !important;
}

.event_type_3 {
  background-color: $purpleBlue !important;
}

