.standard-stand-content-wrapper {
  margin-top: 64px;
}

.standard-stand-form-wrapper {
  display: flex;
  justify-content: center;

  & .callback-form {
    margin-bottom: 0;
  }
}

.equipment-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  margin-top: 16px;
  margin-bottom: 64px;
}

// To visitors page

.photo-item {
  position: relative;
  overflow: hidden;
  display: block;

  &:hover {
    & .photo-item__mask {
      opacity: 1;
    }

    & .photo-item__img {
      transform: scale(1.05);
    }
  }

  &__mask {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .25);
    @include commonTransition;
  }

  &__img {
    width: 100%;
    height: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include commonTransition;
  }
}

// About complex page

.team-promo {
  margin-top: 32px;
  padding-top: 32px;

  &__main {
