    font-weight: 700;
    line-height: 24px;
    color: $black;
  }

  &__position {
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__team {
    padding-top: 64px;
    border-top: 1px solid rgba(12, 154, 221, 0.4);
  }

  &__team-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
