.arrival-promo {
  padding-top: 40px;
}

.type-cards-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
  margin-top: 64px;
  margin-bottom: 64px;
}

.advertising-content-list-wrapper {
  & h2 {
    margin-top: 0;
  }
}

// About region page

// News page

.news-promo-card {
