// Contacts page

#map {
  height: 400px;
  background-color: aliceblue;
}

.contacts-block {
  &__column {
    &_left {
      box-sizing: border-box;
      padding-right: 40px;
    }

    &_right {
      max-width: 384px;
    }
  }

  &__img-wrapper {
    height: 264px;
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__content {
    box-sizing: border-box;
    width: calc(100% - 16px);
    margin-left: auto;
    padding: 16px 24px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__name {
    margin-bottom: 4px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
  }

  &__info-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 16px;
  }

  &__info-item {
    display: flex;
    align-items: center;

    &:not(:last-of-type) {
      margin-bottom: 16px;
    }

    & svg {
      margin-right: 16px;
      fill: $blue;
    }
  }

  &__link {
    font-size: 15px;
    font-weight: 700;
    line-height: 20px;
    text-decoration: none;
    color: $black;

    &:hover {
      color: $red;
    }
  }

  &__title {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    color: $black;
  }

  &__text {
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  & .default-form {
    margin-top: 32px;
  }
}

.default-form {
  &__input-wrapper {
    position: relative;
    margin-bottom: 16px;

    &:after {
      content: '';
      position: absolute;
      top: 8px;
      right: 8px;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: rgba(162, 166, 175, .6);
    }
  }

  &__input {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    padding: 25px 16px 13px;
    border: 0;
    background-color: rgba(162, 166, 175, .15);
    font-size: 15px;
    color: $black;
  }

  &__input-placeholder {
    position: absolute;
    top: 20px;
    left: 16px;
    font-size: 15px;
    color: $gray;
    @include commonTransition;

    &_active {
      top: 10px;
      font-size: 11px;
      color: $blue;
    }
  }

  &__button {
    padding: 19px 40px;
    border: 0;
    background-color: $red;
    cursor: pointer;

    & span {
      display: inline-block;
      margin-right: 16px;
      font-size: 15px;
      color: $white;
    }

    & svg {
      fill: $white;
    }
  }
}

textarea {
  height: 140px;
  resize: none;
}

.checkbox-item {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
  cursor: pointer;

  &__text {
    margin-left: 16px;
    font-size: 11px;
    line-height: 20px;
    color: $black;

    & a {
      font-size: 11px;
      line-height: 20px;
      color: $blue;
    }
  }
}

input[type='checkbox'],
input[type='radio'] {
  position: relative;
  overflow: hidden;
  width: 1px;
  height: 1px;
  opacity: 0;

  &:checked {
    & + .input-mask {
      &:after {
        display: block;
      }
    }
  }
}

input[type='radio'] {
  & + .input-mask {
    border-radius: 50%;

    &:after {
      border-radius: 50%;
    }
  }
}

.input-mask {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: rgba(162, 166, 175, .2);

  &:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    display: none;
    width: 10px;
    height: 10px;
    background-color: $red;
  }
}

.person-contacts-info {
  box-sizing: border-box;
  display: flex;
  padding: 32px;
  border: 1px solid rgba(12, 154, 221, .4);

  &__column {
    width: 33%;
  }
}

.props-wrapper {
  box-sizing: border-box;
  margin-bottom: 80px;
  padding: 32px;
  border: 1px solid rgba(12, 154, 221, .4);

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-bottom: 16px;
    border-radius: 50%;
    background-color: rgba(12, 154, 221, .1);

    & svg {
      margin: auto;
      fill: $blue;
    }
  }

  &__title {
    margin-bottom: 40px;
    font-size: 24px;
    font-weight: 700;
    color: $black;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;

    &-item {

    }
  }

  &__text {
    margin-bottom: 2px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__value,
  &__link {
    font-size: 13px;
    font-weight: 700;
    line-height: 18px;
    color: $black;
  }

  &__value {
    margin-top: 0;
  }

  &__link {
    text-decoration: none;

    &:hover {
      color: $blue;
    }
  }
}

.subsection-item {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-height: 122px;
  padding: 24px;
  background-color: rgba(162, 166, 175, .1);
  @include commonTransition;

  &:hover {
    background-color: rgba(235, 28, 36, .1);

    & .subsection-item__title {
      color: $red;
    }
  }

  & svg {
    margin-bottom: 16px;
    fill: $red;
  }

  &__title {
    display: inline-block;
    font-size: 13px;
    line-height: 18px;
