/**
 * Popups
 */

/**
 * Custom components
 */

.slider {
  &__main {
    position: relative;
  }

  &__img {
    height: 400px;
    background-color: rgba(12, 154, 221, 0.1);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }
}

.slider-controls {
  position: absolute;
  z-index: 5;
  right: 8px;
  bottom: 8px;
  box-sizing: border-box;
  display: flex;
  width: 284px;
  padding: 17px 24px;
  background-color: $white;

  &__counter {
    width: 40px;
    font-size: 13px;
    color: $gray;

    &-current {
      color: $black;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    margin-left: 28px;
  }

  &__progress {
    position: relative;
    width: 58px;
    height: 1px;
    margin-left: 16px;
    margin-right: 16px;
    background-color: #9DD6F1;

    & svg {
      position: absolute;
    }
  }

  &__btn {
    height: 16px;
    outline: 0;
    font-size: 13px;
    cursor: pointer;
    @include commonTransition;

    &_prev {
      width: 36px;
    }

    &_next {
      width: 44px;
    }

    &.slick-disabled {
      color: $gray;

      &:hover {
        color: $gray;

        & .hover-block {
          &__item {
            &_visible {
              transform: translate3d(0, 0, 0);
            }

            &_hover {
              transform: translate3d(0, 100%, 0);
            }
          }
        }
      }
    }

    &:hover {
      color: $red;
    }

    & .hover-block {
      height: 16px;
    }
  }
}

.default-slider-wrapper {
  margin-top: 80px;

  &__top-row {
    margin-bottom: 40px;
  }

  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    color: $black;
  }

  &__main-row {
  }

  &__bottom-row {
    display: flex;
    justify-content: flex-end;
  }

  &__controls {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    width: 284px;
    margin-top: 40px;
    padding: 17px 24px;
    background-color: rgba(12, 154, 221, .1);
  }

  &__counter {
    display: flex;
    width: 40px;
    margin-right: 24px;
    font-size: 13px;
    color: $gray;

    &-current {
      color: $black;
    }
  }

  &__btns {
    display: flex;
    align-items: center;

    & .owl-prev,
    & .owl-next {
      padding: 0;
      border: 0;
      outline: 0;
      background-color: transparent;

      &.disabled {
        & .hover-block {
          &__item {
            color: $gray;
          }
        }

        &:hover {
          & .hover-block {
            &__item {
              &_visible {
                transform: translate3d(0, 0, 0);
              }

              &_hover {
                transform: translate3d(0, 100%, 0);
              }
            }
          }
        }
      }
    }
  }
}

.sidebar {
  position: absolute;
  top: 626px;
  display: block;
  padding-left: 24px;
  border-left: 1px solid rgba(12, 154, 221, .4);

  &_static {
    position: static;
    margin-top: 120px;
  }

  &_fixed {
    position: fixed;
    top: 142px;

    &-bottom {
      position: absolute;
      top: auto;
      bottom: 120px;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__link {
    display: inline-block;
    vertical-align: top;
    max-width: 160px;

    &:not(:last-of-type) {
      margin-bottom: 48px;
    }

    &:hover {
      & .sidebar {
        &__icon-wrapper {
          background-color: $red;

          & svg {
            fill: $white;
          }
        }

        &__title {
          color: $red;
        }
      }
    }
  }

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #FDE7E8;
    @include commonTransition;

    & svg {
      margin: auto;
      fill: $red;
      @include commonTransition;
    }
  }

  &__title {
    margin-top: 16px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
    @include commonTransition;
  }
}

.inner-page-content {
  &__img {
    width: 100%;
    height: 400px;
    margin-bottom: 64px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }

  & .info-slider {
    margin-bottom: 28px;
  }

  & .info-values-slider {
    width: 100%;
    margin-bottom: 82px;
  }
}

.common-promo-only-content {
  & .common-promo__content {
    margin-left: 0;
    padding-left: 0;
    transform: translateY(0);
  }
}

.common-promo {
  margin-bottom: -28px;

  &_unique {
    margin-bottom: 48px;

    & .common-promo__content {
      margin-left: 0;
      padding-left: 0;
      padding-right: 0;
      transform: translateY(0);
    }
  }

  &__illustration-wrapper {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 400px;
    padding-left: 48px;
    padding-right: 48px;
    border: 1px solid rgba(12, 154, 221, .4);
  }

  &__illustration {
    max-width: 100%;
    max-height: 288px;
    height: auto;
  }

  & .default-video {
    margin-bottom: 0;
  }

  &__img {
    height: 400px;
    background-color: rgba(12, 154, 221, .1);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }

  &__content {
    box-sizing: border-box;
    width: calc(100% - 24px);
    min-height: 164px;
    margin-left: auto;
    padding: 32px;
    background-color: $white;
    transform: translateY(-50%);
  }
}

.content-container {
  b {
    margin-top: 16px;
    margin-bottom: 16px;
    font-size: 15px;
    line-height: 24px;
    color: $black;
  }

  & ul {
    margin-top: 24px;
    margin-bottom: 24px;
    padding-left: 18px;
    list-style: none;

    & li {
      font-size: 15px;
      line-height: 24px;
      color: $black;

      &:before {
        position: absolute;
        left: 0;
        content: '\2022' !important;
        color: $red;
      }

      &:not(:last-of-type) {
        margin-bottom: 8px;
      }
    }
  }

  ol {
    margin-top: 24px;
    margin-bottom: 24px;
    padding-left: 0;
    counter-reset: item;

    & li {
      display: block;
      font-size: 15px;
      font-weight: 400;
      line-height: 24px;
      color: $black;

      &:before {
        content: counters(item, '.') '. ';
        counter-increment: item;
      }

      &:not(:last-of-type) {
        margin-bottom: 14px;
      }

      & ol {
        & ol {
          padding-left: 32px;
        }
      }
    }
  }

  & > ol {
    & > li {
      margin-top: 64px;
      margin-bottom: 32px;
      font-size: 24px;
      font-weight: 700;
      line-height: 30px;
      color: $black;
    }
  }

  & img {
    object-fit: cover;
    width: 100%;
    height: 400px;
    margin-top: 64px;
    margin-bottom: 64px;
  }

  & iframe {
    margin-top: 64px;

    & + h2 {
      margin-top: 0;
    }
  }

  & blockquote {
    position: relative;
    margin: 64px 0;
    padding: 24px 32px 24px 86px;
    border: 1px solid rgba(12, 154, 221, .4);
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    color: $gray;

    &:before {
      content: '';
      position: absolute;
      left: 32px;
      width: 30px;
      height: 24px;
      background-image: url(%=static=%minified-svg/quote.svg);
      background-repeat: no-repeat;
    }
  }
}

.hover-block {
  position: relative;
  overflow: hidden;
  z-index: 2;
  display: block;

  &:hover {
    & .hover-block__item_visible {
      transform: translate3d(0, -100%, 0);
    }

    & .hover-block__item_hover {
      transform: translate3d(0, 0, 0);
    }
  }

  &__item {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    @include commonTransition;

    &_visible {
      transform: translate3d(0, 0, 0);
    }

    &_hover {
      transform: translate3d(0, 100%, 0);
    }
  }
}

.info-list {
  margin-bottom: 80px;
  padding-top: 64px;

  &_unique {
    & .info-item {
      &__img-wrapper {
        max-width: 100%;
        margin-right: 40px;
      }

      &__link {
        & span {
          text-decoration: none;
        }
      }
    }
  }
}

.info-item {
  margin-bottom: 64px;

  &__img-wrapper {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 172px;
    height: 186px;
    padding: 24px;
    border: 1px solid rgba(12, 154, 221, .4);
  }

  &__img {
    max-width: 100%;
    max-height: 138px;
    height: auto;
  }

  &__content {

  }

  &__title {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    color: $black;
  }

  &__description {
    margin-top: 0;
    margin-bottom: 24px;

    & a {
      font-size: 15px;
      line-height: 24px;
      text-decoration: underline;
      color: $red;

      &:hover {
        text-decoration: none;
      }
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;
    padding-top: 8px;
    border-top: 1px solid rgba(12, 154, 221, .4);
  }

  &__link {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &:hover {
      & span {
        text-decoration: none;
      }
    }

    & svg {
      margin-right: 8px;
      fill: $blue;
    }

    & span {
      font-size: 13px;
      line-height: 20px;
      text-decoration: underline;
      color: $blue;
    }
  }
}

.default-video-wrapper {
  margin-top: 64px;
}

.default-video {
  width: 100%;
  height: 400px;
  margin-bottom: 64px;
}

.default-img {
  width: 100%;
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.border-top-block {
  border-top: 1px solid rgba(12, 154, 221, .4);
}

