.event-top-card {
  display: block;

  &:hover {
    & .event-top-card__mask {
      opacity: 1;
    }

    & .event-top-card__img {
      transform: scale(1.05);
    }

    & .event-top-card__title {
      color: $red;
    }
  }

  &_type-1 {
  }

  &_type-2 {
    & .event-top-card__tag {
      top: auto;
      left: 8px;
      bottom: 8px;
    }

    & .event-top-card__img {
      height: 195px;
    }

    & .event-top-card__content {
      margin-left: 0;
      padding-left: 0;
      padding-right: 0;
      padding-bottom: 4px;
      background-color: transparent;
      transform: translateY(0);
    }

    & .event-top-card__title {
      font-size: 18px;
    }
  }

  &__img-wrapper {
    position: relative;
    overflow: hidden;
    background-color: rgba(12, 154, 221, 0.1);
  }

  &__mask {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .25);
    @include commonTransition;
  }

  &__tag {
    position: absolute;
    z-index: 1;
    top: 24px;
    left: 24px;
    padding: 3px 16px;
    background-color: $white;
    font-size: 13px;
    line-height: 24px;
    color: $black;

    &_red {
      border-left: 2px solid $red;
    }

    &_blue {
      border-left: 2px solid $blue;
    }

    &_purpleBlue {
      border-left: 2px solid $purpleBlue;
    }
  }

  &__img {
    height: 490px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include commonTransition;
  }

  &__content {
    position: relative;
    z-index: 2;
    margin-left: 24px;
    padding: 16px 24px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__text {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 13px;
    line-height: 20px;
    color: $gray;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &__title {
    margin-top: 10px;
    margin-bottom: 8px;
    font-size: 22px;
    font-weight: 700;
    color: $black;
    @include commonTransition;
  }
}
