.inner-page-promo {
  padding-top: 20px;
  padding-bottom: 24px;
  border-top: 1px solid rgba(12, 154, 221, .4);

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: rgba(235, 28, 36, .1);

    & svg {
      margin: auto;
    }
  }

  &__icon {
    fill: $red;
  }

  &__content {
    padding-top: 4px;
  }

  &__title {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 22px;
      left: -112px;
      width: 66px;
      height: 1px;
      background-color: $red;
    }
  }

  &__paragraph {
    font-size: 13px;
    line-height: 20px;
    margin-bottom: 0;
    color: $gray;
  }
}
