.team-item {
  &__img {
    object-fit: cover;
    width: 100%;
    height: 264px;
  }

  &__content {
    box-sizing: border-box;
    max-width: calc(100% - 16px);
    margin-left: auto;
    padding: 16px 24px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__name {
    margin-bottom: 4px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
  }

  &__position {
    margin-bottom: 16px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 16px;
  }

  &__link {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &:not(:last-of-type) {
      margin-bottom: 14px;
    }

    & svg {
      margin-right: 16px;
      fill: $blue;
    }

    & span {
      font-size: 15px;
      font-weight: 700;
      line-height: 24px;
      color: $black;
    }
  }
}
