.main-slider-dot {
  box-sizing: border-box;
  padding-right: 24px;

  &__text {
    margin-top: 0;
    margin-bottom: 2px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__title {
    display: inline-block;
    margin-top: 2px;
    margin-bottom: 18px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
    cursor: pointer;
    @include commonTransition;

    &:hover {
      color: $red;
    }
  }
}
