.event-promo-card {
  display: block;
  text-decoration: none;

  &:hover {
    & .event-promo-card__title {
      color: $red;
    }
  }

  &__img {
    height: 300px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
  }

  &__content {
    position: relative;
    z-index: 2;
    box-sizing: border-box;
    width: calc(100% - 24px);
    margin-left: auto;
    min-height: 144px;
    padding: 24px 32px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__date {
    display: inline-block;
    margin-bottom: 8px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__title {
    display: inline-block;
    margin-top: 2px;
    margin-bottom: 4px;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    color: $black;
    @include commonTransition;
  }

  &__description {
    margin-top: 0;
    margin-bottom: 24px;
    font-size: 15px;
    line-height: 24px;
    color: $gray;
  }
}
