.equipment-item {
  &:hover {
    & .equipment-item__img-wrapper {
      border-color: $blue;
    }

    & .equipment-item__caption {
      color: $red;
    }
  }

  &__img-wrapper {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 278px;
    margin-bottom: 8px;
    padding: 16px;
    border: 1px solid rgba(12, 154, 221, .4);
    @include commonTransition;
  }

  &__img {
    max-width: 100%;
    max-height: 246px;
    height: auto;
  }

  &__caption {
    font-size: 15px;
    line-height: 22px;
    color: $black;
    @include commonTransition;
  }
}
