.news-item {
  margin-bottom: -34px;

  &__date-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    & svg {
      fill: $blue;
    }
  }

  &__date {
    margin-left: 16px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__link {
    display: block;

    &:hover {
      & .news-item__mask {
        opacity: 1;
      }

      & .news-item__img {
        transform: scale(1.05);
      }

      & .news-item__title {
        color: $red;
      }
    }
  }

  &__img-wrapper {
    position: relative;
    overflow: hidden;
    height: 300px;
  }

  &__mask {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .25);
    @include commonTransition;
  }

  &__img {
    height: 300px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include commonTransition;
  }

  &__content {
    position: relative;
    z-index: 2;
    box-sizing: border-box;
    width: calc(100% - 24px);
    margin-left: auto;
    padding: 24px 32px;
    background-color: $white;
    transform: translateY(-50%);
  }

  &__title {
    margin-bottom: 16px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
    @include commonTransition;
  }

  &__description {
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }
}
