.register-event {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 254px;
  margin-top: 192px;
  padding-left: 24px;
  border-left: 1px solid rgba(12, 154, 221, 0.4);

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    margin-bottom: 16px;
    border-radius: 50%;
    background-color: $red;

    & svg {
      margin: auto;
      fill: $white;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $red;
  }
}
