.other-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 254px;

  &__title {
    margin-bottom: 48px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    color: $black;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &-item {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      &:hover {
        & .other-items__description {
          color: $red;
        }
      }

      &:not(:last-of-type) {
        margin-bottom: 48px;
      }
    }
  }

  &__date,
  &__description {
    font-size: 13px;
    line-height: 20px;
  }

  &__date {
    margin-bottom: 2px;
    color: $gray;
  }

  &__description {
    margin: 0;
    color: $black;
    @include commonTransition;
  }
}
