.type-card {
  display: inline-block;

  &:hover {
    & .type-card__mask {
      opacity: 1;
    }

    & .type-card__img {
      transform: scale(1.05);
    }

    & .type-card__title {
      color: $red;
    }
  }

  &__img-wrapper {
    position: relative;
    overflow: hidden;
  }

  &__mask {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .25);
    @include commonTransition;
  }

  &__img {
    height: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include commonTransition;
  }

  &__title {
    position: relative;
    z-index: 2;
    box-sizing: border-box;
    width: calc(100% - 24px);
    margin-left: auto;
    padding: 24px 32px;
    background-color: $white;
    transform: translateY(-50%);
    font-size: 22px;
    font-weight: 700;
    line-height: 30px;
    color: $black;
    @include commonTransition;
  }
}
