.recommend-item {
  &__img {
    object-fit: cover;
    max-width: calc(100% - 40px);
    width: 100%;
    height: 278px;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__name {
    margin-bottom: 24px;
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    color: $black;
  }

  &__quote {
    position: relative;
    margin: 0;
    padding-top: 40px;
    font-size: 13px;
    line-height: 20px;
    color: $black;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 24px;
      background-image: url(%=static=%minified-svg/quote.svg);
      background-repeat: no-repeat;
    }
  }
}
