.return-top-level {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 40px;
  padding-left: 24px;
  border-left: 1px solid rgba(12, 154, 221, 0.4);

  &__icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    margin-bottom: 16px;
    border-radius: 50%;
    background-color: rgba(162, 166, 175, .1);

    & svg {
      margin: auto;
    }
  }

  &__icon {
    fill: $gray;
  }

  &__text {
    display: block;
    margin-bottom: 2px;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
  }

  &__link {
    display: inline-block;
    margin-bottom: 40px;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-decoration: none;
    color: $black;

    &:hover {
      color: $red;
    }
  }

  & .arrow-btn {
    transform: rotate(180deg);
  }
}
